body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.dropdown-1 {
  width: 500px;
}

img {
  max-width: 100%;
  width: auto;
  height: auto;
}

body {
  background-image: url(/static/media/background.b3deff1e.png);
  background-color: #cccccc;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
}

.lip-container {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.lip-title {
  background-image: url(/static/media/title.28f4ada6.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 150%;
  height: 150%;
}

.dropdown-group {
  text-align: center;
  position: center;
}

CharacteristicInput {
  margin: 0 auto;
}

BrandInput {
  margin: 0 auto;
}


